<template>
    <div style="width: 600px; margin: auto">
        <h1>Consent forms</h1>

        <p-toolbar style="margin-bottom: 10px">
            <template #left>
                <p-button
                    icon="pi pi-chevron-left"
                    @click="goBack"
                    :disabled="menu != null && menu.name == 'MainMenu'"
                />
                &nbsp;&nbsp;&nbsp;
                <p-button
                    label="Email ID Cards"
                    @click="mailEmailIdForm"
                    v-if="menu != null && menu.name == 'MainMenu'"
                />
            </template>
            <template #right v-if="loading">
                <i class="pi pi-spin pi-spinner" style="font-size: 1.5rem"></i>
            </template>
        </p-toolbar>

        <template v-if="menu">
            <div v-for="(item, index) in menu.items">
                <div>
                    <p-toolbar
                        :key="index"
                        style="margin-bottom: 10px; cursor: pointer;"
                        @click="handleMenuClick(item)"
                    >
                        <template #left>
                            <div style="font-size: 1.5rem; width: 100%;">
                                <span>{{ item.label }}</span>
                            </div>
                        </template>

                        <template #right>
                            <i
                                class="pi pi-arrow-right"
                                v-if="item.subMenu != null"
                            ></i>
                            &nbsp;&nbsp;&nbsp;

                            <span
                                v-if="item.subMenu == null"
                                @click.stop="exportBlankPdf(item)"
                                style="text-decoration:underline; color:blue;"
                                >Export</span
                            >
                        </template>
                    </p-toolbar>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
import { onMounted, ref } from "vue-demi";
import axios from "axios";
import { useStore } from "vuex";
import { NL_FORM_BASE } from "@/constants";
import { useToast } from "primevue/usetoast";
export default {
    setup() {
        const store = useStore();
        const fullMenu = ref([]);
        const menu = ref(null);
        const loading = ref(false);
        const toast = useToast();

        const getMenu = async () => {
            loading.value = true;
            try {
                const { data } = await axios.get(
                    `${NL_FORM_BASE}/nlforms/get_menu.php`
                );
                fullMenu.value = data;
                menu.value = data[0];
            } catch (err) {
            } finally {
                loading.value = false;
            }
        };

        const handleMenuClick = (item) => {
            if (item.subMenu == null) {
                getForm(item.formKey);
            } else {
                changeMneu(item.subMenu);
            }
        };

        const mailEmailIdForm = async () => {
            if (
                store.state.artCycle.patient !== null &&
                store.state.artCycle.patient.id !== null
            ) {
                let url = `${NL_FORM_BASE}/nlforms/email_single_form.php`;
                try {
                    const { data } = await axios.get(url, {
                        params: {
                            demographic_no: store.state.artCycle.patient.id,
                            user_name: store.state.user.username,
                            form_name: "photo-id-card"
                        }
                    });
                    toast.add({
                        severity: "success",
                        summary: "Successful",
                        detail: "Email Sent",
                        life: 3000
                    });
                } catch (error) {
                    toast.add({
                        severity: "warn",
                        summary: "Successful",
                        detail: "Problem occurred when sending email",
                        life: 3000
                    });
                }
            }
        };

        const getForm = async (formKey) => {
            let patientId = 0;
            if (
                store.state.artCycle.patient !== null &&
                store.state.artCycle.patient.id !== null
            ) {
                patientId = store.state.artCycle.patient.id;
            }

            console.log(store.state.artCycle.patient);

            let url = `${NL_FORM_BASE}/nlforms/get_form_url.php`;
            try {
                const { data } = await axios.get(url, {
                    params: {
                        demographic_no: patientId,
                        user_name: store.state.user.username,
                        link_key: formKey
                    }
                });
                window.open(data.url, "_blank");
            } catch (error) {}
        };

        const exportBlankPdf = async (item) => {
            let patientId = 0;
            if (
                store.state.artCycle.patient !== null &&
                store.state.artCycle.patient.id !== null
            ) {
                patientId = store.state.artCycle.patient.id;
            }

            let url = `${NL_FORM_BASE}/nlforms/export_theform_pdf.php`;
            try {
                if (item.bookletUrl) {
                    //console.log(item); // bookletUrl label

                    const { data } = await axios.get(url, {
                        params: {
                            demographicno: patientId,
                            username: store.state.user.username,
                            formname: item.label.replace(" ", ""),
                            html_link: item.bookletUrl
                        }
                    });

                    window.open(data.url, "_blank");
                } else {
                    // console.log(item.formKey);

                    const { data } = await axios.get(url, {
                        params: {
                            demographicno: patientId,
                            username: store.state.user.username,
                            link_key: item.formKey
                        }
                    });
                    window.open(data.url, "_blank");
                }
            } catch (error) {}
        };

        const changeMneu = (menuName) => {
            let index = fullMenu.value.findIndex(
                (item) => item.name == menuName
            );
            menu.value = fullMenu.value[index];
        };

        const goBack = () => {
            if (menu.value.parent) {
                let index = fullMenu.value.findIndex(
                    (item) => item.name == menu.value.parent
                );
                menu.value = fullMenu.value[index];
            }
        };

        onMounted(() => {
            getMenu();
        });

        return {
            menu,
            loading,
            mailEmailIdForm,
            handleMenuClick,
            exportBlankPdf,
            goBack
        };
    }
};
</script>
